"use client"

import type React from "react"

import { useState } from "react"
import { Sparkles, Code, Eye, Upload, Wand2, Save, RefreshCw } from "lucide-react"
import { useRef } from "react";

export default function Home() {
  const [command, setCommand] = useState("")
  const [userId] = useState("default-user")
  const [generatedHtml, setGeneratedHtml] = useState("")
  const [templateId, setTemplateId] = useState("")
  const [changeQuery, setChangeQuery] = useState("")
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [loading, setLoading] = useState(false)
  const [activeTab, setActiveTab] = useState<"preview" | "code">("preview")

  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleGenerate = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setGeneratedHtml("")

    // Simulate API call
    setTimeout(() => {
      const mockHtml = `
        <div class="max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden">
          <div class="p-6">
            <h2 class="text-2xl font-bold text-gray-900 mb-4">Contact Form</h2>
            <form class="space-y-4">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Name</label>
                <input type="text" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="Your name">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Email</label>
                <input type="email" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="your@email.com">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Message</label>
                <textarea class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" rows="4" placeholder="Your message"></textarea>
              </div>
              <button type="submit" class="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors">Send Message</button>
            </form>
          </div>
        </div>
      `
      setGeneratedHtml(mockHtml)
      setLoading(false)
    }, 2000)
  }

  const handleUpdateUI = async () => {
    if (!changeQuery.trim() || !generatedHtml) return
    setLoading(true)

    // Simulate update
    setTimeout(() => {
      setChangeQuery("")
      setLoading(false)
    }, 1500)
  }

  const saveUI = async (feedback: boolean) => {
    alert("UI saved successfully!")
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#18181b] via-[#23272f] to-[#18181b] flex flex-col">
      {/* Header */}
      <header className="py-4 px-6 flex items-center justify-between border-b border-zinc-800 bg-[#18181b]">
        <div className="flex items-center space-x-2">
          <div className="w-8 h-8 bg-gradient-to-tr from-blue-600 to-purple-600 rounded-lg flex items-center justify-center">
            <Sparkles className="w-5 h-5 text-white" />
          </div>
          <span className="text-xl font-semibold text-white">AI UI Generator</span>
        </div>
        <nav className="hidden md:flex space-x-8">
          <a href="#" className="text-zinc-400 hover:text-white transition-colors">Docs</a>
          <a href="#" className="text-zinc-400 hover:text-white transition-colors">Examples</a>
          <a href="#" className="text-zinc-400 hover:text-white transition-colors">API</a>
        </nav>
      </header>

      {/* Main Content */}
      <main className="flex-1 flex flex-col items-center justify-center px-2">
        {/* Results Section */}
        {(generatedHtml || loading) && (
          <div className="w-full max-w-2xl mt-8 mb-32 bg-[#23272f] rounded-2xl shadow-2xl border border-zinc-800 overflow-hidden">
            {/* Tabs */}
            <div className="border-b border-zinc-700 flex">
              <button
                onClick={() => setActiveTab("preview")}
                className={`flex-1 py-3 text-lg font-medium flex items-center justify-center gap-2 transition-colors ${
                  activeTab === "preview"
                    ? "bg-gradient-to-r from-blue-600 to-purple-600 text-white"
                    : "bg-transparent text-zinc-400 hover:text-white"
                }`}
              >
                <Eye className="w-5 h-5" />
                Preview
              </button>
              <button
                onClick={() => setActiveTab("code")}
                className={`flex-1 py-3 text-lg font-medium flex items-center justify-center gap-2 transition-colors ${
                  activeTab === "code"
                    ? "bg-gradient-to-r from-pink-600 to-purple-600 text-white"
                    : "bg-transparent text-zinc-400 hover:text-white"
                }`}
              >
                <Code className="w-5 h-5" />
                Code
              </button>
            </div>
            {/* Content */}
            <div className="p-6 bg-[#18181b] min-h-[400px]">
              {loading ? (
                <div className="flex items-center justify-center py-20">
                  <RefreshCw className="w-8 h-8 text-zinc-500 animate-spin mx-auto mb-4" />
                  <p className="text-zinc-400">Generating your UI...</p>
                </div>
              ) : (
                <div className="min-h-[400px]">
                  {activeTab === "preview" ? (
                    <div className="border border-zinc-700 rounded-lg overflow-hidden bg-zinc-900">
                      <iframe
                        srcDoc={generatedHtml}
                        className="w-full h-[400px] bg-zinc-900"
                        title="UI Preview"
                        sandbox="allow-same-origin"
                      />
                    </div>
                  ) : (
                    <pre className="bg-zinc-900 text-green-400 p-4 rounded-lg overflow-x-auto text-sm font-mono">
                      {generatedHtml}
                    </pre>
                  )}
                </div>
              )}
            </div>
          </div>
        )}

        {/* Refine/Update UI */}
        {generatedHtml && !loading && (
          <div className="w-full max-w-2xl mb-36 bg-[#23272f] rounded-2xl shadow-lg border border-zinc-800 p-6">
            <h3 className="text-lg font-semibold text-white mb-4">Refine Your UI</h3>
            <div className="flex gap-3">
              <input
                type="text"
                value={changeQuery}
                onChange={(e) => setChangeQuery(e.target.value)}
                placeholder="Describe changes (e.g., 'Make the button blue and larger')"
                className="flex-1 px-4 py-3 border border-zinc-700 rounded-xl bg-zinc-900 text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
              <button
                onClick={handleUpdateUI}
                disabled={!changeQuery.trim()}
                className="px-6 py-3 bg-blue-600 text-white rounded-xl hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                Update
              </button>
              <button
                onClick={() => saveUI(true)}
                className="flex items-center space-x-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
              >
                <Save className="w-4 h-4" />
                <span>Save UI</span>
              </button>
            </div>
          </div>
        )}
      </main>

      {/* Input Bar at Bottom */}
      <form
        onSubmit={handleGenerate}
        className="fixed bottom-0 left-0 w-full bg-[#18181b] border-t border-zinc-800 px-4 py-4 flex items-center gap-3 z-50"
        style={{ boxShadow: "0 -2px 16px 0 rgba(0,0,0,0.2)" }}
      >
        <button
          type="button"
          onClick={() => fileInputRef.current?.click()}
          className="flex items-center justify-center w-12 h-12 rounded-xl bg-zinc-800 hover:bg-zinc-700 transition-colors"
          title="Upload reference image"
        >
          <Upload className="w-6 h-6 text-zinc-400" />
        </button>
        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={e => setImageFile(e.target.files?.[0] || null)}
          className="hidden"
        />
        <input
          type="text"
          value={command}
          onChange={(e) => setCommand(e.target.value)}
          placeholder="Describe your UI (e.g., 'Modern login form with logo')"
          className="flex-1 px-4 py-4 text-lg border-none rounded-xl bg-zinc-800 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          type="submit"
          disabled={loading || !command.trim()}
          className="flex items-center gap-2 px-8 py-4 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl font-semibold text-lg hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
        >
          {loading ? (
            <>
              <RefreshCw className="w-5 h-5 animate-spin" />
              <span>Generating...</span>
            </>
          ) : (
            <>
              <Sparkles className="w-5 h-5" />
              <span>Generate</span>
            </>
          )}
        </button>
      </form>
    </div>
  );
}
